import "./media/styles/app.scss";
import {
  BrowserRouter,
  Outlet,
  Route,
  Routes,
  useLocation,
} from "react-router-dom";
import { useEffect, useState } from "react";
import Footer from "./footer";
import HomePage from "./pages/home/page";
import Navbar from "./navbar";
import PackagePage from "./pages/package/page";
import PricingPage from "./pages/pricing/page";
import { SWRConfig } from "swr";
import SearchPage from "./pages/search/page";
import { Toast } from "bootstrap";
import { dataFetcher } from "./data";

const swrConfig = {
  fetcher: dataFetcher,
  onErrorRetry: (error, key, config, revalidate, { retryCount }) => {
    console.error(error);
    const notifyError = () => {
      config.appNotifyError(
        ((error.response || {}).data || {}).message
          ? error.response.data.message
          : error
      );
    };
    if (error && error.response && error.response.status < 500) {
      if (error.response.status === 404) window.location.href = "/404.html";
      notifyError();
      return;
    }
    if (retryCount >= 5) {
      notifyError();
      return;
    }
    setTimeout(() => revalidate({ retryCount }), config.errorRetryInterval);
  },
};

export default function App() {
  useEffect(() => {
    const toastElList = [].slice.call(document.querySelectorAll(".toast"));
    toastElList.forEach((item) => new Toast(item).show());
  });
  const [swrError, setSwrError] = useState(null);
  return (
    <SWRConfig
      value={{
        ...swrConfig,
        ...{ appNotifyError: setSwrError },
      }}
    >
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Layout />}>
            <Route
              path="/packages/:type/:ownername/:name/*"
              element={<PackagePage />}
            />
            <Route path="/search" element={<SearchPage />} />
            <Route path="/pricing" element={<PricingPage />} />
            <Route index element={<HomePage />} />
          </Route>
        </Routes>
      </BrowserRouter>
      {swrError ? renderError(swrError) : null}
    </SWRConfig>
  );
}

function Layout() {
  const location = useLocation();
  const isHome = location.pathname === "/";
  return isHome ? (
    <div className="page-home">
      <Navbar className="py-lg-3" />
      <main>
        <Outlet />
      </main>
      <Footer />
    </div>
  ) : (
    <>
      <Navbar className="bg-dark" />
      <main>
        <Outlet />
      </main>
      <Footer />
    </>
  );
}

function renderError(error) {
  return (
    <div
      className="toast position-fixed top-0 end-0 align-items-center text-white bg-danger border-0 m-2"
      style={{ zIndex: 50 }}
      role="alert"
      aria-live="assertive"
      aria-atomic="true"
      data-bs-autohide="false"
    >
      <div className="d-flex">
        <div className="toast-body">{error.toString()}</div>
        <button
          type="button"
          className="btn-close btn-close-white me-2 m-auto"
          data-bs-dismiss="toast"
          aria-label="Close"
        ></button>
      </div>
    </div>
  );
}
